import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import ReactTooltip from "react-tooltip";

function FinalScoreCard(props)
{
    return (<>
            <ReactTooltip />
            <div className="rankOne">
                <div data-tip="Winner"><FontAwesomeIcon icon={['fas', 'medal']} size="lg" className="medalOne"/></div>
                <div><img src={"https://avatars.dicebear.com/api/human/"+props.scoreBoard[0].name+".svg"} alt="random profile" className="profileImg"/></div>
                <div><strong>{props.scoreBoard[0].name}</strong></div>
                <div><strong>1</strong></div>
            </div>
            {
                props.scoreBoard.length > 3 && (<div className="ranking">
                    <div className="rankTwo">
                <div data-tip="1st Runner up"><FontAwesomeIcon icon={['fas', 'medal']} size="lg" className="medalTwo"/></div>
                <div><img src={"https://avatars.dicebear.com/api/human/"+props.scoreBoard[1].name+".svg"} alt="random profile" className="profileImg"/></div>
                <div><strong>{props.scoreBoard[1].name}</strong></div>
                <div><strong>2</strong></div>
                </div>
                <div className="rankThree">
                <div data-tip="2nd Runner up"><FontAwesomeIcon icon={['fas', 'medal']} size="lg" className="medalThree"/></div>
                <div><img src={"https://avatars.dicebear.com/api/human/"+props.scoreBoard[2].name+".svg"} alt="random profile" className="profileImg"/></div>
                <div><strong>{props.scoreBoard[2].name}</strong></div>
                <div><strong>3</strong></div>
                </div>
                </div>)
            }
            <hr/>
            <table>
            <tr>
                <td><div><strong>Profile</strong></div></td>
                <td><div><strong>Name</strong></div></td>
                <td><div><strong>Score</strong></div></td>
                <td><div><strong>Rank</strong></div></td>
            </tr>
    {props.scoreBoard.map((scores,index)=>{
        return <tr>
            <td><div><img src={"https://avatars.dicebear.com/api/human/"+scores.name+".svg"} alt="random profile" className="profileImg"/></div></td>
            <td><div><strong>{scores.name}</strong></div></td>
            <td><div><strong>{scores.score}</strong></div></td>
            <td><div><strong>{index+1}</strong></div></td>
        </tr>
    })}
    </table>
    </>);
}

export default FinalScoreCard;