import {useState} from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import ClockModal from "./ClockModal.js";
import ReactTooltip from "react-tooltip";

function Player(props)
{
    const url = "https://avatars.dicebear.com/api/human/"+props.name+".svg";
    const [currentPoints,setCurrentPoint] = useState(props.score);
    const [changePointsBy,setChangePointsBy] = useState(1);
    const [showModal,setShow] = useState(false);

    function handleShowModal()
    {
        setShow(true);
    }
    function handleHideModal()
    {
        setShow(false);
    }
    function handleIncrement()
    {
        setCurrentPoint(currentPoints+changePointsBy);
        props.onCallback(props.name,currentPoints+changePointsBy);
        setChangePointsBy(1);
    }

    function handleDecrement()
    {
        setCurrentPoint(currentPoints-changePointsBy);
        props.onCallback(props.name,currentPoints-changePointsBy);
        setChangePointsBy(1);
    }

    function handleReset()
    {
        setCurrentPoint(0);
        props.onCallback(props.name,0);
    }

    function handleChangeByPoints(event)
    {
        setChangePointsBy(Number(event.target.value));
    }

    return <>
            <ReactTooltip />
            <div id="playerInfo">
            <h3 id="playerName"><img src={url} alt="random profile" className="profileImg"/> <strong>{props.name}</strong> <div className="iconClock" onClick={handleShowModal}>
            <FontAwesomeIcon icon={['fas', 'stopwatch-20']} size="lg" data-tip="Stop Watch"/></div></h3>
        <div className="playerScores">
        <div id="playerPoints"><h4>{currentPoints}</h4></div>
        <div onClick={handleIncrement} className="btn-points" data-tip="Increment">
        <FontAwesomeIcon icon={['fas', 'plus-circle']} size="lg"/>
        </div>
        <div onClick={handleDecrement} className="btn-points" data-tip="Decrement">
        <FontAwesomeIcon icon={['fas', 'minus-circle']} size="lg"/>
        </div>
        <input type="number" name="changePoints" value={changePointsBy} onChange={handleChangeByPoints} className="changeBy"/>
        <div onClick={handleReset} className="btn-points" data-tip="Reset Points">
            <FontAwesomeIcon icon={['fas', 'redo-alt']} size="lg"/>
        </div>
        <div className="iconTrash btn-points" onClick={()=>props.onDelete(props.name)} data-tip="Delete Player">
            <FontAwesomeIcon icon={['fas', 'trash']} size="lg"/>
        </div>
        </div>
    </div><ClockModal onShow={showModal} onCallback={handleHideModal} name={props.name}/></>;
}
export default Player;